h2 {
    margin: 0;
}

.hero {
    box-sizing: border-box;
    width: 100%;
    height: 80vh;
}

/* NAV BAR */

.nav-bar {
    background-color: rgba(0, 48, 73, 1);
    width: 100%;
}

.logo {
    color: rgba(102, 155, 188, 1);
    padding-left: 5%;
}
.nav-bar h2 {
    font-size: 2em;
}
.logo span {
    color: rgba(120, 0, 0, 1);
}

.hero-content {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding-top: 50px;
}

.hero-img {
    /* width: 100%;
    height: auto; */
}

.hero-text {
    /* font-size: 30em; */
}

/* BOOK BANNER */

.book-banner {
    background-color: rgba(0, 48, 73, 1);
    color: rgba(102, 155, 188, 1);
    margin: 25px auto;
    max-width: 70%;
    padding: 3% 0;
    text-align: center;
}

/* BOOKS LIST */
.book-list-container {
    background-color: rgba(0, 48, 73, 1);
    box-sizing: border-box;
    display: grid;
    gap: 15px;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    padding: 30px 30px;
    max-width: 70%;
    margin: 40px auto 0 auto;
}

.book-details {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.book-details h4 {
    color: rgba(102, 155, 188, 1);
}

.book-img {
    width: 200px;
    height: 299px;
    overflow: hidden;
    transition: ease 0.5s all;
    cursor: pointer;
}

.book-img:hover {
    border-radius: 0px 20px 20px 0px;
}

.book-img img {
    width: 100%;
    height: auto;
}

/* CONTACT SECTION */

.Form-section {
    background-color: rgba(253, 240, 213, 1);;
    width: 100%;
    margin-top: 25px;
}

.contact-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 70%;
    margin: 0 auto;
    padding: 25px 0;
}

input {
    width: 70%;
    height: 50px;
    margin: 10px 0;
    border: none;
}

.submit-btn {
    padding: 10px 30px;
    border: none;
    background-color: rgba(0, 48, 73, 1);
    color: rgba(102, 155, 188, 1);
    cursor: pointer;
    margin: 10px 0;
}

/* FOOTER */
.footer {
    background-color: rgba(0, 48, 73, 1);
    width: 100%;
    color: rgba(253, 240, 213, 1);
    text-align: center;
    font-weight: bold;
    padding: 20px 0;
}